import { Wrapper } from '../../components/swap/styleds'
import { useContext } from 'react'
import PageHeader from '../../components/PageHeader'
import { StyledPageCard } from '../../components/earn/styled'
import AppBody from '../AppBody'
import { AutoColumn } from '../../components/Column'
import { ThemeContext } from 'styled-components'
import { useTestMode } from '../../state/user/hooks'
import { ChainId } from '@feswap/sdk'
import { useWeb3React } from '@web3-react/core'

export default function Mode() {
  const theme = useContext(ThemeContext)
  const { chainId } = useWeb3React()
  const [testMode, setTestMode] = useTestMode()

  return (
    <AppBody style={{ marginTop: '60px' }}>
      <StyledPageCard bgColor={'red'}>
        <PageHeader header={'Set Test Mode'} />
        <Wrapper id="issuance-page">
          <fieldset style={{ borderRadius: '8px', border: '1px solid' + theme.primary4 }}>
            <legend style={{ padding: '0px 6px' }}> Select Test Mode </legend>
            <AutoColumn gap="12px">
              <label>
                <input
                  type="radio"
                  id="Simu"
                  name="TestMode"
                  value="Simu"
                  checked={testMode === 'Simu'}
                  onClick={() => setTestMode('Simu')}
                  style={{ margin: '0.8rem 1rem 0rem' }}
                />
                Simulation Mode
              </label>
              {chainId !== ChainId.MATIC && (
                <label>
                  <input
                    type="radio"
                    id="Dev"
                    name="TestMode"
                    value="Dev"
                    checked={testMode === 'Dev'}
                    onClick={() => setTestMode('Dev')}
                    style={{ margin: '0rem 1rem' }}
                  />
                  Development Mode
                </label>
              )}
              {chainId !== ChainId.MATIC && (
                <label>
                  <input
                    type="radio"
                    id="Pre"
                    name="TestMode"
                    value="Pre"
                    checked={testMode === 'Pre'}
                    onClick={() => setTestMode('Pre')}
                    style={{ margin: '0rem 1rem' }}
                  />
                  Pre-production Mode
                </label>
              )}
              {chainId !== ChainId.MATIC && (
                <label>
                  <input
                    type="radio"
                    id="Pub"
                    name="TestMode"
                    value="Pub"
                    checked={testMode === 'Pub'}
                    onClick={() => setTestMode('Pub')}
                    style={{ margin: '0rem 1rem' }}
                  />
                  Public Integration Mode
                </label>
              )}
              {chainId !== ChainId.MATIC && (
                <label>
                  <input
                    type="radio"
                    id="User"
                    name="TestMode"
                    value="User"
                    checked={testMode === 'User'}
                    onClick={() => setTestMode('User')}
                    style={{ margin: '0rem 1rem' }}
                  />
                  User Experience Mode
                </label>
              )}
              {chainId === ChainId.MATIC && (
                <label>
                  <input
                    type="radio"
                    id="Norm"
                    name="TestMode"
                    value="Norm"
                    checked={testMode === 'Norm'}
                    onClick={() => setTestMode('Norm')}
                    style={{ margin: '0rem 1rem 0.8rem' }}
                  />
                  Normal Release Mode
                </label>
              )}
            </AutoColumn>
          </fieldset>
        </Wrapper>
      </StyledPageCard>
    </AppBody>
  )
}
